import React, { useState, useEffect, useCallback } from "react";
import Jumbotron from "react-bootstrap/Jumbotron";
import Row from "react-bootstrap/Row";
import ProjectCard from "./ProjectCard";
import ProjectCardCustom from "./ProjectCardCustom";
import axios from "axios";

const API = "https://api.github.com"; 
const username = "gitchaoslord"; // username
const reposToShow = 6; // how many repos to show
const heading = "Recent Projects"; // component header
const specificRepos = ['Just-Another-Miner','Space-Shooter','Solar-System-demo-with-three.js','Bulk-Cooking-Recipes','Basic-Javascript-Memory-Card-Game']; // repo names

const customRepo = {
  heading:"Inventory Management App",
  description:"Developed for a custom android device Sunmi L2 in order to help the company's staff quickly identify product info and assist in the 'Year-End Inventory' process.",
  languages:["HTML","TypeScript","JavaScript","SCSS"],
  updateDate:'on December 10, 2020'
}
//{"HTML":"12%","TypeScript":"22%","JavaScript":"22%","SCSS":"22%"},
// const Project = ({ heading, username, length, specificRepos }) => {
const Project = () => {
  //REMOVE KEY later
  
  const finalAPI = `${API}/users/${username}/repos?sort=updated&direction=desc`;
  const specificReposAPI = `${API}/repos/${username}`;
  const [projectsArray, setProjectsArray] = useState([]);

  const fetchRepos = useCallback(async () => {
    let reposList = []; // common array for all and specific repos
    if(specificRepos === 0){
      // add all repos up to reposToShow if no specified are selected
      try {
        const response = await axios.get(finalAPI); // Get all repos
        reposList = [...response.data.slice(0, reposToShow)]; // slicing to reposToShow
        setProjectsArray(reposList); // set projectsArray
      } catch (error) {
        console.error(error.message);
      }
    }
    else {
      // adding specified repos
      try { 
        for (let repoName of specificRepos) {
          const response = await axios.get(`${specificReposAPI}/${repoName}`);
          //reposList = [...response.data.slice(0, reposToShow)]; // slicing to reposToShow
          reposList.push(response.data);
        }
        setProjectsArray(reposList);// set projectsArray
      } catch (error) {
        console.error(error.message);
      }
   }

  }, [finalAPI, specificReposAPI]);
// }, [finalAPI, reposToShow, specificRepos, specificReposAPI]); //WARNING FIX
  useEffect(() => {
    fetchRepos();
  }, [fetchRepos]);

  return (
    <Jumbotron fluid className="colorlib-projects" data-section="projects">
      {/* <Container></Container> */}
      <div className="colorlib-narrow-content">
        <div className="row">
          <div className="col-md-6 col-md-offset-3 col-md-pull-3">
            <h2 className="colorlib-heading">{heading}</h2>
          </div>
        </div>
        <Row className="">

          {/* OLD  */}
          {/* {projectsArray.length
            ? projectsArray.map((project, index) => (
                <ProjectCard
                  key={`project-card-${index}`}
                  id={`project-card-${index}`}
                  value={project}
                />
              ))
            : console.log(() => (
              console.log() // find a way to make this work lol
            ))} */}

            {/* Load custom project TODO ARRAYS */}
            <ProjectCardCustom value={customRepo}/>
            {/* projects from githubapi */}
            {projectsArray.map((project, index) => (
                <ProjectCard
                  key={`project-card-${index}`}
                  id={`project-card-${index}`}
                  value={project}
                />
            ))}
        </Row>
      </div>
    </Jumbotron>
  );
};

export default Project;