import React from "react";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";

const ProjectCardCustom = ({value}) => {

    const {
        heading,
        description,
        languages,
        updateDate
    } = value;

    return (
        <Col md={6}>
            <Card className="card p-3 mb-5 shadow-lg bg-white rounded">
                
                <Card.Body>
                
                <Card.Title as="h3">{heading } </Card.Title>
                <Card.Text>{description} </Card.Text>           
                <hr />
                <Language languages={languages}/>
                <CardFooter updateDate={updateDate}/>    
                </Card.Body>
            </Card>
        </Col>
    )
}

const Language = ({ languages }) => {
   
   const array = [];
   for (let x in languages){
    array.push(languages[x])
   }

    return (
        <div className="pb-3">
            Languages:{" "}
            {array.map((language)=>
                (
                    <div className="badge badge-light card-link" >{language}</div>
                )
            )}
        </div> 
    );
};

const CardFooter = ({  updateDate }) => {

    return (
        <p className="card-text">
            <small className="text-muted">Updated {updateDate}</small>
        </p>
    )
};

export default ProjectCardCustom;